import { Directive, ElementRef, OnInit, Input, OnChanges } from '@angular/core';

@Directive({
  selector: '[appLoading]'
})
export class LoadingDirective implements OnInit, OnChanges {
  @Input() appLoading: boolean;
  @Input() loadingSmall: boolean;

  constructor(private el: ElementRef) {}

  ngOnInit() {
    this.checkClass();
    if (this.loadingSmall) {
      this.el.nativeElement.classList.add('loading--small');
    }
  }

  ngOnChanges() {
    this.checkClass();
  }

  checkClass() {
    if (this.appLoading) {
      this.el.nativeElement.classList.add('loading');
    } else {
      this.el.nativeElement.classList.remove('loading');
    }
  }
}



