import { environment } from './../../../../environments/environment';
import { Observable } from 'rxjs/Observable';
import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { Headers, RequestOptions } from '@angular/http';

@Injectable()
export class ApiService {
  headers: Headers;
  options: RequestOptions;

  constructor(private http: Http) {
    this.headers = new Headers();
  }
  /**
   * Формирует GET-запрос
   * @param url
   */
  get(url) {
    return this.responseHandler(this.http.get(this.absUrl(url), this.options));
  }
  /**
   * Формирует POST-запрос и преобразует объект в FormData
   * @param url
   * @param data
   */
  post(url, data) {
    const formData = new FormData();

    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        formData.append(key, data[key]);
      }
    }
    return this.responseHandler(
      this.http.post(this.absUrl(url), formData, this.options)
    );
  }
  /**
   * Обновляет аутентификационный токен в заголовке запросов
   * @param token
   */
  updateAuthToken(token) {
    this.headers.append('auth-token', token);
    this.options = new RequestOptions({ headers: this.headers });
  }
  /**
   * Так как у нас на сервере ошибки декларируются не HTTP-статусом,
   * нужно обрабатывать его вручную и эмитировать состояние ошибки
   *
   * Функция обрабатывает запрос и возвращает <Observable>
   * для дальнейшей работы с сервисом
   * @param url
   */
  responseHandler(request) {
    return new Observable<any>(observer => {
      request.subscribe(
        result => {
          const data = JSON.parse(result._body);
          if (data.code === 0) {
            observer.next(data);
          } else {
            observer.error(data);
          }
        },
        error => {
          observer.error(error);
        }
      );
    });
  }
  /**
   * Генерирует абсолютный путь запроса
   * @param url
   */
  private absUrl(url) {
    return environment.baseUrl + url + '/';
  }
}



