import { Injectable, Output, EventEmitter } from '@angular/core';
import { FacebookService } from 'ngx-facebook';
import { environment } from 'environments/environment';
import { ApiService } from './../api/api.service';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class AuthService {
  authorized: boolean;
  @Output() isAuthorized: EventEmitter<boolean> = new EventEmitter();

  constructor(
    private apiService: ApiService,
    private facebookService: FacebookService
  ) {}
  /**
   * Совершает логин пользователя по телефону/имейлу и паролю
   * @param username
   * @param password
   */
  loginByUsername(username, password) {
    const formData = {
      phone: username,
      password: password
    };

    return this.apiService.post(this.getAuthUrl('signin'), formData);
  }
  /**
   * Инициализирует настройки необходимые для авторизации через соц.сети
   */
  initOAuth() {
    console.log(this.facebookService.getLoginStatus());
  }
  /**
   * Совершает авторизацию через facebook
   */
  loginFacebook() {
    const fbRequest = new Observable(observer => {
      this.facebookService
        .login()
        .then(result => {
          const postData = {
            access_token: result.authResponse.accessToken
          }
          this.apiService
            .post(this.getAuthUrl('facebook_login'), postData)
            .subscribe(
              success => {
                observer.next(success.exists);
              },
              failure => {
                observer.error(failure);
              }
            );
        })
        .catch(error => {
          observer.error(error);
        });
    });
    return fbRequest;
  }
  /**
   * Трансформирует путь обращение к Api сервису.
   */
  private getAuthUrl(resourse) {
    return ['auth', resourse].join('/');
  }
}



