import { FacebookService } from 'ngx-facebook';
import { AuthService, ApiService } from './../../../services';
import { Component, OnInit } from '@angular/core';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-login-modal',
  templateUrl: './login-modal.component.html',
  styleUrls: ['./login-modal.component.scss']
})
export class LoginModalComponent implements OnInit {
  username: string;
  password: string;

  rememberMe = true;
  showPassword = false;

  error = '';
  success = '';

  isLoading: boolean;

  constructor(
    private authService: AuthService,
    private apiService: ApiService,
    public loginModal: NgbActiveModal,
    private fb: FacebookService
  ) {}

  ngOnInit() {}

  /**
   * Закрывает модальное окно
   */
  closeModal() {
    this.loginModal.dismiss('Cross click');
  }
  /**
   * Совершает авторизацию по имени пользователя и пароля
   */
  loginByUsername() {
    this.isLoading = true;
    this.error = '';
    this.authService.loginByUsername(this.username, this.password).subscribe(
      success => {
        this.isLoading = false;
        this.apiService.updateAuthToken(success.token);
        this.success = 'Добро пожаловать';

        // this.closeModal();
      },
      error => {
        this.isLoading = false;
        this.error = error.message;
      }
    );
  }

  /**
   * Показывает/скрывает пароль для пользователя
   */
  togglePassword() {
    this.showPassword = !this.showPassword;
  }

  /**
   * Совершает авторизацию через google
   */
  loginGoogle() {}

  /**
   * Совершает авторизацию через facebook
   */

  loginFacebook() {
    this.authService.loginFacebook().subscribe(exists => {
      if (exists === true) {
        this.success = 'Добро пожаловать';
      } else {
        this.error =
          'Вы еще не зарегистрированы, пожалуйста пройдите регистрацию';
      }
    });
  }
  /**
   * Совершает авторизацию через Vkontakte
   */
  loginVkontakte() {}

  /**
   * Совершает авторизацию через Instagram
   */
  loginInstagram() {}

  /**
   * Открывает окно регистрации
   */
  openRegister() {}
}



